body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to left, #44581660 ,#44680440 ), url(/gifly/static/media/background.45a01c85.jpg);
  background-position: center;
  /* background-repeat: no-repeat; */
  /* background-size: cover; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.cardWrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.heading-primary--main {
    color: #777;
    font-size: 40px;
}

.heading-primary--sub {
    font-size: 20px;
}
input[type='text'] {
    width: 50%;
    height: 50px;
    padding: 10px;
}

button[type='button'] {
    width: 120px;
    height: 50px;
    padding: 10px;
}

.searchForm {
    margin-bottom: 50px;
}
.card {
    position: relative;
    display: inline-block;
}

img {
    border-radius: 10px;
    position: relative;
    margin: 10px;
}

.download {
    position: absolute;
    bottom: 10px;
    right: 10px;
    color: #fff;
    background-color: #55555570;
    padding: 3px;
    border-radius: 4px;
    font-size:15px;
}
